/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #e1e1e1;
  color: #C5371B;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #C5371B;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #C5371B;
  font-size: 2rem;
}

/* Estilos para el menú de navegación */
.menu {
  background-color: #C5371B; /* Color de fondo */
  padding: 20px;
  width: 200px;
  max-height: 100vh; /* Altura máxima igual a la altura de la pantalla */
  overflow-y: auto; /* Desplazamiento vertical si el contenido excede el tamaño */
  color: #ffffff; /* Color del texto */
  position: fixed;
  top: 0;
  left: 0;
}
.menu a {
  display: block;
  color: #ffffff;
  text-decoration: none;
  padding: 30px 20px; /* Espaciado interno */
  transition: background-color 0.3s ease; /* Transición suave de color de fondo */
}
.menu a:hover {
  background-color: #C5371B; /* Color de fondo al pasar el ratón */
}

@media (min-width: 768px) {
  .contenedor-app {
    min-height: 100vh;
    width: calc(100% - 430px); /* Ajuste para dejar espacio al menú */
    margin: 2rem auto;
    padding: 3rem;
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Sombra */
    background-color: #FAF1F2; /* Fondo blanco */
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../../img/img.png);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.barra {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #C5371B;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

table {
  border-collapse: collapse;
  width: 100%;
  border-radius: 12px;
  overflow: hidden; /* Para ocultar los bordes redondeados internos */
}

th, td {
  border: 1px solid #e1e1e1;
  padding: 13px;
  text-align: left;
}

th {
  background-color: #C5371B;
  color: #FAF1F2;
}

td {
  background-color: #FFFFFF;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: white;
  color: #C5371B;
  cursor: not-allowed;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #C5371B;
  display: block;
  margin-bottom: 5rem;
  text-align: center;
}

.boton {
  background-color: #C5371B;
  padding: 1.5rem 4rem;
  color: #FAF1F2;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #982a15;
  cursor: pointer;
}